const api = {
    //loginUser: (username, password) => api.post('auth/login', {username, password}),
  
    saveItem: (saveData) => apiW.post(`save_item`,saveData),
    saveOrganization: (saveData) => apiW.post(`save_organization`,saveData),

    getOrganization: (id) => apiW.get(`organization/${id}`),

    overview: () => apiW.get('overview'),
    //curId - org - next - unrated
    getItem: (org, curr, mode, mode2) => apiW.get(`item/next/${curr}/${org}/${mode}/${mode2}`),
  
    getSpecific: (org, item) => apiW.get(`item/${org}/${item}`),
  
    saveData: (org, item, saveData) => apiW.post(`rating/${org}/${item}`,saveData)
};

export default api;
