import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { Switch, Route, HashRouter } from 'react-router-dom';
import Header from './Header';
import Rating from './Rating';
import Rating2 from './Rating2';
import Table from './Table';
import Organization from './Organization';
import New from './New';
import NewItem from './NewItem';
//import Edit2 from './Edit2';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Dashboard = () => (
  <div className="container">
    <h2>Welcome</h2>
    Welcome to EST LAT article writing contest to celebrate 100th birthday of both countries!
  </div>
)

const NotFound = ({ location }) => (
  <div>
    <h3>Did not found page for <code>{location.pathname}</code></h3>
  </div>
)

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoggedIn: false,
      userName: null
    }
  }
  /*
  componentDidMount() {
    fetch("https://tools.wmflabs.org/edgars/estlat.php?act=userinfo")
        .then(res => res.json())
        .then(
          (result) => {
            if (!('error' in result)){
              {this.setState({isLoggedIn: true,userName:result['query']['userinfo']['name']})}
          }
        },
          (error) => {
            //set state
          }
        )
  }
*/
  render() {
    return (
      <HashRouter>
        <div>
          <Header isLogged={this.state.isLoggedIn} userName={this.state.userName} />
          <Switch>
            <Route exact path="/" component={Table} />
            <Route exact path="/table" component={Table} />
            <Route exact path="/organization/:org" component={Organization} />
            {/*<Route exact path="/edit/:id" component={Edit} />*/}
            <Route exact path="/rating/:org/:item" component={Rating2} />
            <Route exact path="/new/organization" component={New} />
            <Route exact path="/new/item" component={NewItem} />
            {/*<Route exact path="/new" component={New} />    ()=><New />*/}
            <Route component={NotFound} />
          </Switch>
          <ToastContainer
          position="bottom-right"
          autoClose={2500}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnVisibilityChange
          draggable={false}
          pauseOnHover
          />
        </div>
      </HashRouter>
    );
  }
}

export default App;
