//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { toast } from 'react-toastify';
import {Tooltip, OverlayTrigger} from 'react-bootstrap';
import {Select, Label, Checkbox} from "./FormElements";
import Description from "./Description";
import History from "./History";

import api from '../api';

const capacities = {
  0:'N/A',
  1:'none',
  2:'low',
  3:'medium',
  4:'high'
};

const robustnesses = {
  0:'N/A',
  1:'none',
  2:'low',
  3:'medium',
  4:'high'
};

class Rating extends Component {
  constructor(props) {
    super(props);
    this.state = {
      initialData: {},
      itemId: 0,//should be ok
      textPlaceholders: {},
      capacity: null,
      robustness: null,
      dataHistory: [],
      irrelevant: false,
      nextMode: 'next',
      toSaveData: {},
      loadingData: true,
    };
    this.saveToDB = this.saveToDB.bind(this);
    this.handleSkip = this.handleSkip.bind(this);
  }
  
  handleChange(key, event) {
    const value = event.target.type === 'checkbox' ? event.target.checked : parseInt(event.target.value);
    
    var toSaveData = {...this.state.toSaveData};
    toSaveData[key] = value;
    //key - parametra nosaukums
    //event.value - input/textarea lauka vērtība
    
    this.setState({ toSaveData });
    //console.log(this.state[key]);

    /////////////////issue data
    this.setState({ [key]: value });
  }

  handleSkip() {
    this.setup();
  }

  setup() {
    //api.getItem(this.state.organizationId,'next')
    //  .then((response) => this.setStating(response));

    //fetch(`//localhost/ccmB/public//item/next/${this.state.organizationId}/next`)

    //api.getItem(this.state.organizationId,'next')
    //api.getItem(this.state.itemId,this.state.organizationId,'next','unrated')
    //(org, curr, mode, mode2)
    //curId - org - next - unrated
    api.getItem(this.state.organizationId,this.state.itemId,'next','unrated')
    //api.getSpecific(this.state.organizationId,'1')
      .then((data) => {
        let cap;
        let robust;
        let irrel;
        let itemId;
        
        if (data.desc.status === 'error') {
          console.log('itemId');
          itemId = 0;
        }
        
        //debugger;
        if (data.rating.status === 'ok') {
          console.log('iiiiii',data.desc.data.id);
          itemId = data.desc.data.id;
          cap = data.rating.data.capacity === null ? 0 : data.rating.data.capacity;
          robust = data.rating.data.robustness === null ? 0 : data.rating.data.robustness;
          irrel = data.rating.data.irrelevant === null ? false : data.rating.data.irrelevant;
        } else {
          itemId = typeof data.desc.data !== 'undefined' ? data.desc.data.id : 0;
          cap =0;
          robust = 0;
          irrel = false;
          console.log(data.desc);
        }

        const dataHist = data.history.status === 'ok' ? data.history.data : [];

        this.setState({
          loadingData: false,
          itemId: itemId,
          toSaveData: {},
          dataHistory: dataHist,
          textPlaceholders: data.desc.data ? data.desc.data : {},
          capacity: cap,
          robustness: robust,
          irrelevant: irrel,
          initialData: {
              capacity: cap,
              robustness: robust,
            irrelevant: irrel
          }
        });

      });

      /*
      
    this.props.history.push({
      pathname: '/'+this.props.match.params.list+ (langValue ? '/'+langValue : '')
    })
    */
  }
  
  saveToDB(e) {
    e.preventDefault();
    //console.log(this.state);
    const _this = this;
    const {toSaveData, initialData, capacity, robustness} = this.state;
  
    if (Object.keys(toSaveData).length === 0) {
      toast.info('Data was not changed');
      console.log('nav ko saglabāt');
      return;
    }
  
    //console.log(toSaveData);
    // && (issueData.public === "")
    
    let reallyChanged = {};
  
    for(let key in toSaveData) {
      if (toSaveData[key] !== initialData[key]) {
        reallyChanged[key] = toSaveData[key];
      }
    }
    
    if (Object.keys(reallyChanged).length === 0) {
      toast.info('Data was not changed');
      return;
    }

    console.log('to save',reallyChanged);

    if (('irrelevant' in reallyChanged && reallyChanged['irrelevant'] === true) && ('capacity' in reallyChanged || 'robustness' in reallyChanged)) {
      console.log('here');
      reallyChanged = Object.assign({}, {irrelevant:reallyChanged.irrelevant});
    }
    console.log('here2',reallyChanged);

    if ('capacity' in reallyChanged || 'robustness' in reallyChanged) {
      const isInvalid = [capacity, robustness].some(x => x === 0);

      if (isInvalid && (capacity + robustness > 0)) {
        toast.info('Have to fill both capacity and robustness');//šis nav ok, ja abi ir N/A
        return;
      }
    }

    
    api.saveData(this.state.organizationId,this.state.itemId,reallyChanged)
    //api.getSpecific(this.state.organizationId,'1')
      .then((data) => {
        console.log(data);
        _this.setup();
        window.scrollTo(0, 0);
        toast.success('Saved data!');
    });
    
    //sending to db!
    //_this.setup();
  }

  componentDidMount() {
      //console.log(this.props);
      this.setState({organizationId: this.props.organization}, () => this.setup());//, () => this.setup()
      //this.setup();
      //this.setup();
  }
  
  componentDidUpdate(prevProps) {
      //:org/:item
    if (this.props.organization !== prevProps.organization) {
        this.setState({organizationId: this.props.organization}, () => this.setup());
    }
  }
  
  render() {
    const {textPlaceholders, dataHistory, irrelevant, loadingData} = this.state;
    const disabledSelects = irrelevant ? true : false;
  
    return (
        <div className="container">
        {loadingData ? <div>Data is loading</div> : <div>
        {Object.keys(textPlaceholders).length > 0 ? <div><Description id={this.state.itemId} textPlaceholders={textPlaceholders} capacity={this.state.capacity} />
            {dataHistory.length > 0 ? <History data={dataHistory} /> : ""}
            <h3>Edit data</h3>
            <form>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"This isn't relevant for us"} name={"irrelevant"}/>
                <Checkbox checkClass={"col-sm-4"} name={"irrelevant"} value={this.state.irrelevant} handleChange={this.handleChange.bind(this, 'irrelevant')} />
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Your capacity"} name={"capacity"}/>
                <Select disabled={disabledSelects} selectClass={"col-sm-4"} name={"capacity"} placeholder={"Select value"} value={this.state.capacity} handleChange={this.handleChange.bind(this, 'capacity')}  options={Object.entries(capacities).map(stat => ({'value':stat[0],'label':stat[1]}))}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Your robustness"} name={"robustness"}/>
                <Select disabled={disabledSelects} selectClass={"col-sm-4"} name={"robustness"} placeholder={"Select value"} value={this.state.robustness} handleChange={this.handleChange.bind(this, 'robustness')}  options={Object.entries(robustnesses).map(stat => ({'value':stat[0],'label':stat[1]}))}/>
            </div>
            </form>
            <button type="button" className="btn btn-primary" onClick={this.saveToDB}>Save!</button>
            <button type="button" className="btn btn-primary" onClick={this.handleSkip}>Skip</button>
            </div> : <div>No data to show, everything is filled</div>}
            </div>}
      </div>
    );
  }
}
//{this.state.irrelevant ? "" : <div>

export default Rating;