import React, { Component } from 'react';
import Rating from './Rating';
import api from '../api';

export default class Organization extends Component {
  constructor(props) {
    super(props);
    //this.handleChange = this.handleChange.bind(this);
	//this.componentDidMount = this.componentDidMount.bind(this);
    this.state = {
      posts: [],
	  updated: '',
	  isLoaded: false,
	  hasError: false,
    filter: "",
    language: "en"
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    //this.setState({filter: event.target.value});
    this.props.handleFilterChange(event.target.value);
  }

  setup() {

    const orgId = this.props.match.params.org;
    console.log('orgId',orgId);

    api.getOrganization(orgId)
      .then((data) => {
        if (data.status === 'ok') {
          console.log();
          this.setState({
              'title':data.data.name,
              'orgId':orgId,
              orgType: data.data.org_type
          });

        }
        

      });


  }

  componentDidMount() {
      this.setup();
  }
  
	componentDidUpdate(prevProps) {
        if (this.props.match.params.org !== prevProps.match.params.org) {
            this.setup();
        }
	}

  render() {
    const {title, orgType, orgId } = this.state;
    return <div className="container">
        <h3>{title} <small>({orgType})</small></h3>
        <br />
        {orgId ? <Rating organization={this.state.orgId} /> : ""}
	</div>;
  }
}