const Select = (props) => {
  //console.log(props.name,props.value);
  return(
          <div className={props.selectClass}>
            {/*defaultValue={props.defaultValue}*/}
          <select
            className="form-control"
            name={props.name}
            value={props.value}// ? props.value : ""
            onChange={props.handleChange}
            disabled={props.disabled}
            >
            <option value="" disabled>{props.placeholder}</option>
            {props.options.map(option => {
              return (
                <option
                  key={option.value}
                  value={option.value}
                  label={option.label}>{option.label}
                </option>
              );
            })}
          </select>
          </div>)
}

const Select2 = (props) => {
  //console.log(props.name,props.value);
  return(
          <div className={props.selectClass}>
            {/*defaultValue={props.defaultValue}*/}
          <select
            className="form-control"
            name={props.name}
            value={props.value ? props.value : ""}
            onChange={props.handleChange}
            disabled={props.disabled}
            >
            <option value="" disabled>{props.placeholder}</option>
            {props.options.map(option => {
              return (
                <option
                  key={option.value}
                  value={option.value}
                  label={option.label}>{option.label}
                </option>
              );
            })}
          </select>
          </div>)
}
const Textarea = (props) => {
  return(
          <div className={props.inputClass}>
            {/*defaultValue={props.defaultValue}*/}
            <textarea className="form-control" value={props.value} onChange={props.handleChange} />
          </div>)
}

const Checkbox = (props) => {
  return(

          <div className={props.checkClass}>
            {/*defaultValue={props.defaultValue}*/}
    <input
    name={props.name}
            type="checkbox"
            checked={props.value}
            onChange={props.handleChange} />
          </div>
          )
}


export {Input, Label, Select, Select2, Textarea, Checkbox};