//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';

class Description extends Component {
    

  render() {
    const {textPlaceholders, capacity} = this.props;

    return (
        <div>
            <h2>{textPlaceholders.title} <small>({textPlaceholders.category})</small></h2>
            <h3>Description</h3>
            <br />
            <div className="desc"><i>Description</i>: {textPlaceholders.description}</div>
            <br />
            <div className="relevant"><i>Who is this relevant to</i>: {textPlaceholders.relevant}</div>
            <br />
            Scores:
            <table className="table table-striped  table-bordered table-sm">
  <thead>
    <tr>
      <th scope="col" style={{textAlign: 'center'}}>Capacity level</th>
      <th scope="col" style={{textAlign: 'center'}}>Description</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>none</td>
      <td className={capacity === 1 ? "table-success" : ""}>{textPlaceholders.desc_none ? textPlaceholders.desc_none : ""}</td>
    </tr>
    <tr>
      <td>low</td>
      <td className={capacity === 2 ? "table-success" : ""}>{textPlaceholders.desc_low ? textPlaceholders.desc_low : ""}</td>
    </tr>
    <tr>
      <td>medium</td>
      <td className={capacity === 3 ? "table-success" : ""}>{textPlaceholders.desc_med ? textPlaceholders.desc_med : ""}</td>
    </tr>
    <tr>
      <td>high</td>
      <td className={capacity === 4 ? "table-success" : ""}>{textPlaceholders.desc_high ? textPlaceholders.desc_high : ""}</td>
    </tr>
  </tbody>
</table>
      </div>
    );
  }
}
//{this.state.irrelevant ? "" : <div>

export default Description;