import React, { Component } from 'react';
import ReactTable from "react-table";
import 'react-table/react-table.css';

/*
capacity	1
robustness	1
user	132
add_time	2018-12-23 17:28:28
*/

const capacities = {
    '0':'N/A',
    '1':'none',
    '2':'low',
    '3':'medium',
    '4':'high'
  };
  
  const robustnesses = {
    '0':'N/A',
    '1':'none',
    '2':'low',
    '3':'medium',
    '4':'high'
  };
  
class History extends Component {
    render() {
      const { data } = this.props;
      
      return (<div>
          <h3>Data history</h3>
          <ReactTable
            data={data}
            columns={[
                {
                  Header: "Edit time",
                  accessor: "add_time"
                },
                {
                  Header: "Capacity",
                  accessor: d => capacities[d.capacity],
                  id: "capacity"
                },
                {
                  Header: "Robustness",
                  accessor: d => robustnesses[d.robustness],
                  id: "robustness"
                },
                {
                  Header: "Irrelevant?",
                  accessor: "irrelevant"
                },
                {
                  Header: "User",
                  accessor: d => d.user,
                  id: "user"
                }
              ]}
              showPagination={true}
            defaultPageSize={10}
            defaultSorted={[
              {
                id: "add_time",
                desc: true
              }
            ]}
            minRows={0}
            className="-striped -highlight"
          />
          </div>
      );
    }
}

export default History;