//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { toast } from 'react-toastify';
import {Input, Select2, Label, Checkbox} from "./FormElements";

import api from '../api';

const types = {
  '0':'user group',
  '1':'Wikimedia community',
  '2':'Wikimedia affiliate'
};

class New extends Component {
  constructor(props) {
    super(props);
    this.state = {
      initialData: {},
      itemId: 0,//should be ok
      textPlaceholders: {},
      capacity: null,
      robustness: null,
      dataHistory: [],
      irrelevant: false,
      nextMode: 'next',
      toSaveData: {},
      loadingData: true,
    };
    this.saveToDB = this.saveToDB.bind(this);
  }
  
  handleChange(key, event) {
    const value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
    
    var toSaveData = {...this.state.toSaveData};
    toSaveData[key] = value;
    //key - parametra nosaukums
    //event.value - input/textarea lauka vērtība
    
    this.setState({ toSaveData });
    //console.log(this.state[key]);

    /////////////////issue data
    this.setState({ [key]: value });
  }
  
  saveToDB(e) {
    e.preventDefault();
    const _this = this;
    const {toSaveData, initialData, capacity, robustness} = this.state;
  
    if (Object.keys(toSaveData).length === 0) {
      toast.info('Data was not changed');
      console.log('nav ko saglabāt');
      return;
    }
  
    //console.log(toSaveData);
    // && (issueData.public === "")
    
    let reallyChanged = {};
  
    for(let key in toSaveData) {
      if (toSaveData[key] !== initialData[key]) {
        reallyChanged[key] = toSaveData[key];
      }
    }

    if (!('type' in reallyChanged)) {
      toast.info('Please select organization type!');
      return;
    }

    if (!('title' in reallyChanged)) {
      toast.info('Please add title');
      return;
    }

    
    if (Object.keys(reallyChanged).length === 0) {
      toast.info('Data was not changed');
      return;
    }

    //console.log('to save',reallyChanged);

    
    api.saveOrganization(reallyChanged)
      .then((data) => {
        //console.log(data);
        //_this.setup();
        toast.success('Saved data!');
        _this.setState({title:'',link:'',type:null,initialData: {},toSaveData: {}});
    });
    
    //sending to db!
    //_this.setup();
  }

  /*
  componentDidMount() {
      //console.log(this.props);
      this.setState({organizationId: this.props.organization}, () => this.setup());//, () => this.setup()
      //this.setup();
      //this.setup();
  }
  
  componentDidUpdate(prevProps) {
      //:org/:item
    if (this.props.organization !== prevProps.organization) {
        this.setState({organizationId: this.props.organization}, () => this.setup());
    }
  }
  */
  render() {
    //const {formType} = this.props;
    
    return (
        <div className="container">
            <h3>Add new organization</h3>
            <form>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Title"} name={"title"}/>
                <Input inputClass={"col-sm-4"} name={"title"} value={this.state.title} handleChange={this.handleChange.bind(this, 'title')} placeholder={"Wiki loves people user group"}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Link"} name={"link"}/>
                <Input inputClass={"col-sm-4"} name={"link"} value={this.state.link} handleChange={this.handleChange.bind(this, 'link')} placeholder={"https://en.wikipedia.org"}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Organization type"} name={"type"}/>
                <Select2 selectClass={"col-sm-4"} name={"type"} placeholder={"Select value"} value={this.state.type} handleChange={this.handleChange.bind(this, 'type')}  options={Object.entries(types).map(stat => ({'value':stat[0],'label':stat[1]}))}/>
            </div>
            </form>
            <button type="button" className="btn btn-primary" onClick={this.saveToDB}>Save!</button>
      </div>
    );
  }
}
//{this.state.irrelevant ? "" : <div>

export default New;