import React, { Component } from 'react';
import ReactTable from "react-table";
import 'react-table/react-table.css';
import api from '../api';

const rating = (data,type) => {
  let icon, title;
  if (data === '4') {
    icon = "glyphicon glyphicon-arrow-up";
    title = "high";
  } else if (data === '3') {
    icon = "glyphicon glyphicon-resize-horizontal";
    title = "medium";
  } else if (data === '2') {
    icon = "glyphicon glyphicon-arrow-down";
    title = "low";
  } else if (data === '1') {
    icon = "glyphicon glyphicon-ban-circle";
    title = "none";
  } else if (data === '-1') {
  icon = "glyphicon glyphicon-remove-circle";
  title = "N/A";
}

title = title + " " + type;

  return <span className={icon} title={title} aria-hidden="true"></span>;
};
const oneCell = (data,itemId,orgId) => {
  let ratingComponent;
  if (data === undefined) {
    ratingComponent = "";
  } else if (data.irrelevant !== null) {
    ratingComponent = rating('-1','');
  } else {
    ratingComponent = <span>{rating(data.capacity,'capacity')} | {rating(data.robustness,'robustness')}</span>
  }
  //const {} = data;
  //console.log(data);

  let linkComponent;

  if (orgId === undefined || itemId === undefined) {
    linkComponent = "";
  } else {
    linkComponent = <small>(<a href={`#/rating/${orgId}/${itemId}`} target="_blank">edit</a>)</small>;
  }

  return <span>{ratingComponent} {linkComponent}</span>;
};

const orgTitle = (data) => {
  const {link, title, id} = data;
  let theLink;

  if (link !== undefined && link !== null) {
    theLink = <a href={link} target="_blank">{title}</a>;
  } else {
    theLink = title;
  }

  return <span>{theLink} <small>(<a href={`#/organization/${id}`} target="_blank">edit</a>)</small></span>
};

const legend = () => {
  let legend;

  legend = <ul>
    <li>{rating('-1','')} N/A</li>
    <li>{rating('1','')} none</li>
    <li>{rating('2','')} low</li>
    <li>{rating('3','')} medium</li>
    <li>{rating('4','')} high</li>
  </ul>

  return legend;
};

class Table extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loadingData: true,
      loadingError: false,
      data: [],
    };
  }
  
  componentDidMount() {
    //api - kur ir 'error' zars?
    api.overview()
      .then((data) => {
        this.setState({data, loadingData: false});//, () => this.setup()
      });
}

  render() {
    const { data } = this.state;
    
      return <div className="container">
      {this.state.loadingData ? <div>Loading...</div> :
          <div>{data.length>0 ? <div><ReactTable
            data={data}
            columns={[
                {
                  Header: "Info",
                  columns:[
                      {
                        Header: "Organization",
                        accessor: d => orgTitle(d.details),
                        id: "title",
                        style: {whiteSpace: 'normal'}
                      }
                    ]
                },
                {
                  Header: "Communications and media relations",
                  columns:[
                      {
                        Header: "Social media",
                        accessor: d => (<span>{oneCell(d.ratings['1'],1,d.details.id)}</span>),
                        id: "soc_media"
                      },
                      {
                        Header: "Press",
                        accessor: d => (<span>{oneCell(d.ratings['2'],2,d.details.id)}</span>),
                        id: "press"
                      }
                    ]
                },
                {
                  Header: "Community Health",
                  columns:[
                      {
                        Header: "Conflict Resolution",
                        accessor: d => (<span>{oneCell(d.ratings['8'],8,d.details.id)}</span>),
                        id: "conflict"
                      },
                      {
                        Header: "Abuse and Harassment Control",
                        accessor: d => (<span>{oneCell(d.ratings['9'],9,d.details.id)}</span>),
                        id: "abuse"
                      },
                      {
                        Header: "Tools",
                        accessor: d => (<span>{oneCell(d.ratings['10'],10,d.details.id)}</span>),
                        id: "tools"
                      },
                      {
                        Header: "Regular newbie-friendly in-person events",
                        accessor: d => (<span>{oneCell(d.ratings['11'],11,d.details.id)}</span>),
                        id: "newbie",
                        style: {whiteSpace: 'normal'}
                      },
                      {
                        Header: "Help and tutorial resources",
                        accessor: d => (<span>{oneCell(d.ratings['12'],12,d.details.id)}</span>),
                        id: "help"
                      },
                      {
                        Header: "Recognition",
                        accessor: d => (<span>{oneCell(d.ratings['13'],13,d.details.id)}</span>),
                        id: "recognition"
                      },
                      {
                        Header: "OTRS handling",
                        accessor: d => (<span>{oneCell(d.ratings['14'],14,d.details.id)}</span>),
                        id: "otrs",
                        style: {whiteSpace: 'normal'}
                      }
                    ]
                },
              ]}
            defaultPageSize={10}
            defaultSorted={[
              {
                id: "title",
                desc: false
              }
            ]}
            showPagination={true}
            minRows={0}
            className="-striped -highlight"
          />
          <br />
          <b>Legend</b>
          {legend()}
          </div> : <div>No data to show</div>}</div>}
        </div>
  }
}


export default Table;