//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { toast } from 'react-toastify';
import {Input, Select2, Label, Checkbox} from "./FormElements";

import api from '../api';

class NewItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      initialData: {},
      toSaveData: {}
    };
    this.saveToDB = this.saveToDB.bind(this);
  }
  
  handleChange(key, event) {
    const value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
    
    var toSaveData = {...this.state.toSaveData};
    toSaveData[key] = value;
    //key - parametra nosaukums
    //event.value - input/textarea lauka vērtība
    
    this.setState({ toSaveData });
    //console.log(this.state[key]);

    /////////////////issue data
    this.setState({ [key]: value });
  }
  
  saveToDB(e) {
    e.preventDefault();
    console.log(this.state);
    const _this = this;
    const {toSaveData, initialData, capacity, robustness} = this.state;
  
    if (Object.keys(toSaveData).length === 0) {
      toast.info('Data was not changed');
      console.log('nav ko saglabāt');
      return;
    }
  
    //console.log(toSaveData);
    // && (issueData.public === "")
    
    let reallyChanged = {};
  
    for(let key in toSaveData) {
      if (toSaveData[key] !== initialData[key]) {
        reallyChanged[key] = toSaveData[key];
      }
    }

    if (!('category' in reallyChanged)) {
      toast.info('Please add category');
      return;
    }

    if (!('title' in reallyChanged)) {
      toast.info('Please add title');
      return;
    }

    
    if (Object.keys(reallyChanged).length === 0) {
      toast.info('Data was not changed');
      return;
    }

    //console.log('to save',reallyChanged);

    
    api.saveItem(reallyChanged)
      .then((data) => {
        console.log(data);
        //_this.setup();
        toast.success('Saved data!');
        _this.setState({toSaveData:{},title:"",category:"",description:"",relevant:"",desc_none:"",desc_low:"",desc_med:"",desc_high:"",examples:""});
    });
    
    
    //sending to db!
    //_this.setup();
  }

  /*
  componentDidMount() {
      //console.log(this.props);
      this.setState({organizationId: this.props.organization}, () => this.setup());//, () => this.setup()
      //this.setup();
      //this.setup();
  }
  
  componentDidUpdate(prevProps) {
      //:org/:item
    if (this.props.organization !== prevProps.organization) {
        this.setState({organizationId: this.props.organization}, () => this.setup());
    }
  }
  */
  render() {
    //const {formType} = this.props;
    
    return (
        <div className="container">
            <h3>Add new item</h3>
            <form>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Title"} name={"title"}/>
                <Input inputClass={"col-sm-4"} name={"title"} value={this.state.title} handleChange={this.handleChange.bind(this, 'title')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Category"} name={"category"}/>
                <Input inputClass={"col-sm-4"} name={"category"} value={this.state.category} handleChange={this.handleChange.bind(this, 'category')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Description"} name={"description"}/>
                <Input inputClass={"col-sm-4"} name={"description"} value={this.state.description} handleChange={this.handleChange.bind(this, 'description')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Relevant to who?"} name={"relevant"}/>
                <Input inputClass={"col-sm-4"} name={"relevant"} value={this.state.relevant} handleChange={this.handleChange.bind(this, 'relevant')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Description to level none"} name={"desc_none"}/>
                <Input inputClass={"col-sm-4"} name={"desc_none"} value={this.state.desc_none} handleChange={this.handleChange.bind(this, 'desc_none')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Description to level low"} name={"desc_low"}/>
                <Input inputClass={"col-sm-4"} name={"desc_low"} value={this.state.desc_low} handleChange={this.handleChange.bind(this, 'desc_low')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Description to level medium"} name={"desc_med"}/>
                <Input inputClass={"col-sm-4"} name={"desc_med"} value={this.state.desc_med} handleChange={this.handleChange.bind(this, 'desc_med')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Description to level high"} name={"desc_high"}/>
                <Input inputClass={"col-sm-4"} name={"desc_high"} value={this.state.desc_high} handleChange={this.handleChange.bind(this, 'desc_high')} placeholder={""}/>
            </div>
            <div className="row">
                <Label labelClass={"col-sm-2"} title={"Examples"} name={"examples"}/>
                <Input inputClass={"col-sm-4"} name={"examples"} value={this.state.examples} handleChange={this.handleChange.bind(this, 'examples')} placeholder={""}/>
            </div>
            </form>
            <button type="button" className="btn btn-primary" onClick={this.saveToDB}>Save!</button>
      </div>
    );
  }
}
//{this.state.irrelevant ? "" : <div>

export default NewItem;